import { useEffect, useMemo, useState } from 'react';
import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useSnackbar } from 'notistack';
import { m } from 'framer-motion';
// @mui
import { useTheme, styled } from '@mui/material/styles';
import { Box, Grid, Card, Link, Stack, Button, Divider, Container, Typography, Fade, Modal, Backdrop } from '@mui/material';
// _mock_
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// components
import { LoadingButton } from '@mui/lab';
import StripeCheckout from 'react-stripe-checkout';
import { varFade, MotionViewport } from '../../components/animate';
import axiosInstance from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';
// ----------------------------------------------------------------------

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 500,
  bgcolor: 'background.paper',
  // border: '2px solid #000',
  borderRadious: '10px',
  boxShadow: 24,
  p: 4,
};

const RootStyle = styled('div')(({ theme }) => ({
  padding: theme.spacing(10, 0),
  backgroundColor: theme.palette.background.neutral,
  [theme.breakpoints.up('md')]: {
    padding: theme.spacing(15, 0),
  },
}));

// ----------------------------------------------------------------------

export default function HomePricingPlans() {

  const theme = useTheme();
  const [Package, setPackage] = useState([])

  const isLight = theme.palette.mode === 'light';

  const Packages = async () => {
    try {
      const response = await axiosInstance.get('user/view/packages');
      console.log(response, "package")
      setPackage(response?.data?.data)
    } catch (error) {
      console.log(error, "package")
    }
  }

  useEffect(() => {
    Packages();
  }, [])



  return (
    <RootStyle>
      <Container component={MotionViewport}>
        <Box sx={{ mb: 10, textAlign: 'center' }}>
          <m.div variants={varFade().inUp}>
            <Typography component="div" variant="overline" sx={{ mb: 2, color: 'text.disabled' }}>
              pricing plans
            </Typography>
          </m.div>
          <m.div variants={varFade().inDown}>
            <Typography variant="h2" sx={{ mb: 3 }}>
              The right plan for your business
            </Typography>
          </m.div>
          <m.div variants={varFade().inDown}>
            <Typography
              sx={{
                color: isLight ? 'text.secondary' : 'text.primary',
              }}
            >
              Choose the perfect plan for your needs. Always flexible to grow
            </Typography>
          </m.div>
        </Box>

        <Grid container spacing={5}>
          {Package.map((plan) => (
            <Grid key={plan.license} item xs={12} md={4}>
              <m.div >
                <PlanCard plan={plan} />
              </m.div>
            </Grid>
          ))}
        </Grid>

        <m.div variants={varFade().in}>
          <Box sx={{ p: 5, mt: 10, textAlign: 'center' }}>
            <m.div variants={varFade().inDown}>
              <Typography variant="h3">Still have questions?</Typography>
            </m.div>

            <m.div variants={varFade().inDown}>
              <Typography sx={{ mt: 3, mb: 5, color: 'text.secondary' }}>
                Please describe your case to receive the most accurate advice.
              </Typography>
            </m.div>

            <m.div variants={varFade().inUp}>
              <Button
                size="large"
                variant="contained"
                href="mailto:support@minimals.cc?subject=[Feedback] from Customer"
              >
                Contact us
              </Button>
            </m.div>
          </Box>
        </m.div>

      </Container>
    </RootStyle>
  );
}

// ----------------------------------------------------------------------

PlanCard.propTypes = {
  plan: PropTypes.shape({
    license: PropTypes.string,
    commons: PropTypes.arrayOf(PropTypes.string),
    icons: PropTypes.arrayOf(PropTypes.string),
    options: PropTypes.arrayOf(PropTypes.string),
  }),
};

function PlanCard({ plan }) {
  const { enqueueSnackbar } = useSnackbar();
  const [open, setOpen] = useState(false);
  const [OrderId, setOrderId] = useState();
  const [Amount, setAmount] = useState();
  const handleClose = () => setOpen(false);
  const [isLoading, setIsLoading] = useState(false);
  // eslint-disable-next-line react/prop-types
  const { package_amount: amount, package_description: description, package_name: name, id } = plan;

  const handlePlans = (id,amount) => {
    setOpen(true)
    setOrderId(id)
    setAmount(amount)
  };


  const FormSchema = Yup.object().shape({
    fname: Yup.string().required('First Name is required'),
    lname: Yup.string().required('Last Name is required'),
    email: Yup.string().email('Invalid email').required('Email is required'),
  });

  const defaultValues = useMemo(
    () => ({
      fname: '',
      lname: '',
      email: '',

    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const methods = useForm({
    resolver: yupResolver(FormSchema),
    defaultValues,
  });

  const {
    reset,
    watch,
    // control,
    setValue,
    handleSubmit,
    formState: { isSubmitting, errors },
  } = methods;

  const values = watch();
  // console.log(errors, "errors")
  // console.log(values, "values")

  const handlePaymentAndSubmit = async (token) => {
    setIsLoading(true);
    try {
      const formData = new FormData();
      formData.append('first_name', methods.getValues('fname'));
      formData.append('last_name', methods.getValues('lname'));
      formData.append('email', methods.getValues('email'));
      formData.append('source', token.id); // Include Stripe token in form data
      formData.append('amount', Amount); // Include Stripe token in form data
      formData.append('subscription_id', OrderId); // Include Stripe token in form data

      // Example API call using axios to submit all data together
      const response = await axiosInstance.post('user/buy/package', formData);

      if (response.status === 200 || response.status === 201) {
        const responseData = response.data;
        console.log(responseData);
        enqueueSnackbar(responseData?.message,{
          variant: "success"
        })
        handleClose();
        // Reset form or perform other actions after successful payment and form submission
      }
    } catch (error) {
      console.error('Error submitting payment and form:', error);
      enqueueSnackbar(error?.message,{
        variant: "error"
      })
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      <Card
        sx={{
          p: 5,
          boxShadow: (theme) => theme.customShadows.z24
        }}
      >
        <Stack spacing={5} sx={{ height: "50vh", display: "flex", justifyContent: "space-between", alignItems: "center", flexDirection: "column" }}>
          <Typography variant="h4">{name}</Typography>
          <Stack sx={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
            <Typography variant="h4" sx={{ color: "#103472" }}>${amount}</Typography>
          </Stack>
          <Typography variant="h4">{description}</Typography>

          <Button
            size="large"
            fullWidth
            variant={'contained'}
            onClick={() => handlePlans(id,amount)}
          >
            Choose Plan
          </Button>
        </Stack>
      </Card>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        onClose={handleClose}
        closeAfterTransition
        slots={{ backdrop: Backdrop }}
        slotProps={{
          backdrop: {
            timeout: 500,
          },
        }}
      >
        <Fade in={open}>
          <Box sx={style}>
            <FormProvider methods={methods} onSubmit={handleSubmit()}>
              <Grid item xs={12} md={12}>
                <Typography variant='h5' sx={{ mb: 2 }} >Please Fill Form for buy Paclage</Typography>
                {/* <Card sx={{ p: 3 }}> */}
                <Box
                  sx={{
                    display: 'grid',
                    columnGap: 2,
                    rowGap: 3,
                    gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
                  }}
                >

                  <RHFTextField
                    name="fname"
                    label="First Name"
                  />
                  <RHFTextField
                    name="lname"
                    label="Last Name"
                  />
                  <RHFTextField
                    name="email"
                    label="Email"
                  />

                </Box>
                {/* </Card> */}
                <Stack justifyContent="flex-end" flexDirection={'row'} gap={1} sx={{ mt: 3 }}>

                  <StripeCheckout
                    token={(token) => handlePaymentAndSubmit(token)}
                    stripeKey="pk_test_51IIZO8Kp3j5Y1UYUhdopZryeRKjlRqEcw4rrDhwELKCRDjRSOJtSXicia6jkN63CyflKvQwej7SqGt1MZ6UwKgfz00ipRMArG9" // Replace with your Stripe publishable key
                  >
                    <LoadingButton type="submit" variant="contained" loading={isLoading}>
                      Pay with Card
                    </LoadingButton>
                  </StripeCheckout>
                </Stack>
              </Grid>
            </FormProvider>
          </Box>
        </Fade>
      </Modal>
    </>
  );
}
